
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Clipboard from 'clipboard';

export default {
directives: {
TransferDom
},
data () {
return {
title:'操作提示',
description: '请复制以下网址https://ipcrs.pbccrc.org.cn/在手机浏览器打开',
icon: 'warn',
message:'https://ipcrs.pbccrc.org.cn/'
}
},
created(){

},
components: {
XHeader,
PopupHeader,
Actionsheet,
Msg ,
FormPreview,XButton
},
methods:{
copy() {
var clipboard = new Clipboard('.tag-read')
clipboard.on('success', e => {
this.$vux.toast.show({
text: '复制成功',
type:'success'
})

clipboard.destroy()
})
clipboard.on('error', e => {

this.$vux.toast.show({
text: '该浏览器不支持自动复制',
type:'warn'
})

clipboard.destroy()
})
},
goLast(){
let pengyuan_reporStatus = this.$route.query.pengyuan_reporStatus;
this.$router.push({
path:'/py_credit',
query: {
pengyuan_reporStatus: pengyuan_reporStatus
}
});
}
}
}



